/*height of navbar from top of page is 70px, 
darkest color #102b42 - rgb(16, 43, 66) then #0e273e slightly lighter - rgb(14, 39, 62)
lightest is #f1f5f8
color of A logo background and entire navbar is #0e273e
*/

.navbar {
  background: #102b42;
  height: 70px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 1.2rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}

#Allie_Logo {
  display: inline-block;
  top: 9px;
  margin-top: 2px;
  margin-left: 1.1rem;
}

/* .navbar > h1 {
  color: #f1f5f8;
  font-size: 35px;
  top: 0;
} */

.navbar-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 70px;
  max-width: 1500px;
  top: 0;
}

/* margin-left: 20px OR 0.5rem, test this!*/

.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  list-style: none;
  text-align: center;
  width: 60vw;
  -webkit-justify-content: end;
          justify-content: end;
}

.nav-item {
  display: inline-block;
  height: 70px;
}

.nav-links {
  color: #f1f5f8;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  text-decoration: none;
  padding: 2rem 1rem;
  height: 100%;
  cursor: pointer;
}

.nav-links:hover {
  border-bottom: 4px solid #f1f5f8;
  transition: all 0.1s ease-out;
}

.fa-bars {
  color: #f1f5f8;
  margin-right: 1.3rem;
}

/* .nav-links-mobile {
  display: none;
} */

.menu-icon {
  display: none;
}

/* @media screen and (max-width: 960px) {
  body {
    overflow-x: hidden;
  }
} */

@media screen and (max-width: 1020px) {
  .navbar > h1 {
    color: #f1f5f8;
    font-size: 32px;
  }
}

@media screen and (max-width: 940px) {
  .NavbarItems {
    position: relative;
    width: 100%;
  }

  .nav-menu {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 35%;
    height: 50vh;
    position: absolute;
    top: 70px;
    right: -100%;
    opacity: 1;
    transition: all 0.5s ease;
    padding-top: auto;
    padding-bottom: auto;
  }

  .nav-menu.active {
    background: #0e273e;
    right: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    text-align: middle;
    -webkit-justify-content: center;
            justify-content: center;
  }

  .nav-links {
    text-align: center;
    -webkit-justify-content: center;
            justify-content: center;
    color: #f1f5f8;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    padding-top: 0.3rem;
    padding-left: 0.2rem;
  }

  .nav-links:hover {
    background-color: #f1f5f8;
    color: #0e273e;
    border-radius: 2px;
  }

  .nav-item {
    height: 20%;
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #f1f5f8;
    font-size: 2rem;
    margin-right: 1.3rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #f1f5f8;
    padding: 14px 20px;
    border: 1px solid #f1f5f8;
    transition: all 0.3 ease-out;
  }

  .nav-links-mobile:hover {
    background: #f1f5f8;
    color: #0e273e;
    transition: 250ms;
  }
}

@media screen and (max-width: 500px) {
  .navbar > h1 {
    visibility: hidden;
  }
} 


